import style from './index.module.scss'
import { Table } from 'antd'

const column = [
    {
        key: 'ckey',
        dataIndex: 'ckey',
        render: (text: string) => (
            <h6 style={{ fontWeight: 'bold' }}>{text}</h6>
        ),
    },
    {
        key: 'value',
        dataIndex: 'value',
    },
]

const dataSource = [
    {
        key: '1',
        ckey: 'Tên người dùng',
        value: 'Lê Văn Dũng',
    },
    {
        key: '2',
        ckey: 'Email',
        value: 'Dungvl@gmail.com',
    },
    {
        key: '3',
        ckey: 'Tên đăng nhập',
        value: 'Da2.levandung',
    },
    {
        key: '4',
        ckey: 'Số điện thoại',
        value: '0123456789',
    },
    {
        key: '5',
        ckey: 'Ngày sinh',
        value: '30/4/2020',
    },
    {
        key: '6',
        ckey: 'Địa chỉ',
        value: '',
    },
    {
        key: '7',
        ckey: 'Chức vụ',
        value: 'Quản trị dự án',
    },
    {
        key: '8',
        ckey: 'Ngày thêm',
        value: '30/10/2121',
    },
    {
        key: '9',
        ckey: 'Chỉnh sửa lần cuối',
        value: '22/01/2121',
    },
]

const ModeratorProjectModal = () => {
    return (
        <div className={style.moderator_project_container}>
            <Table
                className={style.moderator_project_table_content}
                columns={column}
                dataSource={dataSource}
                showHeader={false}
                pagination={false}
            />
            <div className={style.moderator_project_avatar}>
                <h6>Ảnh đại diện</h6>
                <img
                    alt="avatar"
                    src="data:image/png;base64,"
                />
            </div>
        </div>
    )
}

export default ModeratorProjectModal
